export default {
	onLoad (options) {
		this.status = options.status
		
		this.payAmount = options.payAmount

		if (this.payAmount === "undefined") {
			this.payAmount = 0
		}
		
		this.initDiscountInfo()
	},
	data () {
		return {
			scrollList: ['未使用','已使用','已过期'],
			currentIndex: 0,
			//优惠券
			disCountList: [],
			//折扣券
			sell: [],
			status:'',
			payAmount: 0
		}
	},
	methods:{
		goBacktitle(){
			uni.navigateBack({
				delta:1
			})
		},
		//返回
		goBack(item) {

			if (this.currentIndex == 1) {
				uni.showToast({
					icon:'none',
					title: '该券已被使用'
				})
				return
			}
			if (this.currentIndex == 2) {
				uni.showToast({
					icon:'none',
					title: '该券已过期'
				})
				return
			}
			if (item.type) {

				if (this.payAmount < 50) {
					uni.showToast({
						icon: 'none',
						title: '订单金额小于50，无法使用该优惠券'
					})
					return
				}
			}
			
			if (item && item != undefined) {
				uni.setStorageSync('discount',item)	
			}
			
			if (this.status === "0") {
				uni.navigateTo({
					url:'../storage/storage?car=' + 0 + '&scrollIndex=' + 0
				})
			} else {
				uni.navigateBack({
					delta:1
				})
			}
			
		},
		handleScroll (index) {
			this.currentIndex = index
			this.initDiscountInfo()
		},
		//优惠券列表信息
		async initDiscountInfo () {

			let res = await this.$fetch(this.$api.disCount,{status:this.currentIndex},"POST","Form")

			if (res.code == 0) {
				//优惠券
				let disCountList = []
				//折扣卷
				let sell = []
				res.rows.filter(function(value,index){
					if (value.type === 0) {
						disCountList.push(value)
					}
					if (value.type === 1) {
						sell.push(value)
					}
				})

				this.disCountList = disCountList
				this.sell = sell

			}
			
		},
		goToRichText (id) {
			
			uni.navigateTo({
				url: '../rich-text/rich-text?id=' + id
			})
		},
		unDiscount () {
			console.log('discount')
			uni.removeStorageSync('discount')
			uni.navigateBack({
				delta: 1
			})
		}
	}
}
